import { _decorator, Component, instantiate, Label, Node, Prefab, view } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Game')
export class Game extends Component {
    @property({type: Label})
    private scoreLabel: Label;
    private score: number = 0;
    @property({type: Prefab})
    private blockPrefab: Prefab;
    private gap: number = 20;
    @property({type: Node})
    private background: Node;
    private blockSize: number = 0;

    drawBgBlocks() {
        this.blockSize = (view.getVisibleSize().width - this.gap * 5) / 4;
    }

    start() {
        this.drawBgBlocks();
        var x = this.gap + this.blockSize / 2;
        var y = x;
        var block = instantiate(this.blockPrefab);
    }

    update(deltaTime: number) {
        
    }
}

